import React, { useEffect, useState, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import * as actions from '../store/actions/index';

const { kakao } = window;

const KakaoMap = ({ history }) => {
  const [isMapSupported, setIsMapSupported] = useState(true);
  const [location, setLocation] = useState('');
  const [addr, setAddr] = useState('');
  const [longitude, setLongitude] = useState(null);
  const [latitude, setLatitude] = useState(null);

  const dispatch = useDispatch();

  useEffect(() => {
    mapScript();
  }, [])

  // ÏΩúÎ∞± Ìï®Ïàò
  const locationBtnHandler = useCallback((event) => {
    event.preventDefault();
    // set ÌïòÍ∏∞Ï†ÑÏóê, Ïûò Ï†ÅÏóàÎäîÏßÄ ÏúÑÏπò ÌïÑÌÑ∞ÎßÅ ÌïúÎ≤à Ìï¥ÏïºÌï®.
    const locationRegex = /^(ÏÑúÏö∏|Í≤ΩÍ∏∞ÎèÑ|Í∞ïÏõêÎèÑ|Ï∂©Ï≤≠Î∂ÅÎèÑ|Ï∂©Ï≤≠ÎÇ®ÎèÑ|Ï†ÑÎùºÎ∂ÅÎèÑ|Ï†ÑÎùºÎÇ®ÎèÑ|Í≤ΩÏÉÅÎ∂ÅÎèÑ|Í≤ΩÏÉÅÎÇ®ÎèÑ|Î∂ÄÏÇ∞|Ï†úÏ£º|ÏÑ∏Ï¢Ö|ÎåÄÍµ¨|Ïù∏Ï≤ú|Í¥ëÏ£º|ÎåÄÏ†Ñ|Ïö∏ÏÇ∞)/;

    if(!addr) {
      if(!location.match(locationRegex))
          return alert('Ïò¨Î∞îÎ•∏ ÏßÄÏó≠ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
    }

    const finalLocation = addr || location;
    dispatch(actions.addLocation(finalLocation, longitude, latitude));
    history.push('/setting/2'); 
  }, [addr, location, longitude, latitude]);

  const locationTextChangeHandler = useCallback((event) => {
    setLocation(event.target.value);
  }, []);


  // Îü∞ÌÉÄÏûÑÏóê Ïã§Ìñâ
  const mapScript = () => {
      const container = document.getElementById('map'); 
      const options = {
        center: new kakao.maps.LatLng(0, 0),
        level: 5
      };

      const map = new kakao.maps.Map(container, options);
      let marker = null;
      const geocoder = new kakao.maps.services.Geocoder();
      const infowindow = new kakao.maps.InfoWindow({zindex:1}); // ÌÅ¥Î¶≠Ìïú ÏúÑÏπòÏóê ÎåÄÌïú Ï£ºÏÜåÎ•º ÌëúÏãúÌï† Ïù∏Ìè¨ÏúàÎèÑÏö∞ÏûÖÎãàÎã§
      
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(function(position) {
            const lat = position.coords.latitude;
            const lon = position.coords.longitude;
            setLatitude(lat);
            setLongitude(lon);
            const locPosition = new kakao.maps.LatLng(lat, lon); 
            
            displayMarker(locPosition);

            // latitude, longitudeÎ°ú ÌòÑÏû¨ ÏúÑÏπòÏ†ïÎ≥¥ Î∞òÌôòÌïòÎäî ÏΩúÎ∞±Ìï®Ïàò.
            searchDetailAddrFromCoords({ lat: lat, lng: lon}, function(result, status) {
              if (status === kakao.maps.services.Status.OK) {
                const fullAddr = result[0].address.address_name; 
                const newAddr = fullAddr.split(' ');
                setAddr(newAddr[0] + ' ' + newAddr[1]);
              }
            });
          });
      } else{
          alert("ÌòÑ Î∏åÎùºÏö∞Ï†ÄÏóêÏÑú GeolocationÏùÑ ÏßÄÏõêÌïòÏßÄ ÏïäÏäµÎãàÎã§.");
          setIsMapSupported(false);
      }
      
      // ÏßÄÎèÑÏóê ÎßàÏª§ÏôÄ Ïù∏Ìè¨ÏúàÎèÑÏö∞Î•º ÌëúÏãúÌïòÎäî Ìï®ÏàòÏûÖÎãàÎã§
      function displayMarker(locPosition) {
          marker = new kakao.maps.Marker({  
              map: map, 
              position: locPosition
          }); 
          // ÏßÄÎèÑ Ï§ëÏã¨Ï¢åÌëúÎ•º Ï†ëÏÜçÏúÑÏπòÎ°ú Î≥ÄÍ≤ΩÌï©ÎãàÎã§
          map.setCenter(locPosition);      
      }

      // ÏßÄÎèÑÎ•º ÌÅ¥Î¶≠ÌñàÏùÑ Îïå ÌÅ¥Î¶≠ ÏúÑÏπò Ï¢åÌëúÏóê ÎåÄÌïú Ï£ºÏÜåÏ†ïÎ≥¥Î•º ÌëúÏãúÌïòÎèÑÎ°ù Ïù¥Î≤§Ìä∏Î•º Îì±Î°ùÌï©ÎãàÎã§
      kakao.maps.event.addListener(map, 'click', function(mouseEvent) {
        searchDetailAddrFromCoords(mouseEvent.latLng, function(result, status) {
            if (status === kakao.maps.services.Status.OK) {
                let detailAddr = !!result[0].road_address ? result[0].road_address.address_name : '';
                detailAddr += result[0].address.address_name;
                
                const fullAddr = result[0].address.address_name; 
                const newAddr = fullAddr.split(' ');
                const displayAddr = '<p style="margin: 10px;"> ' + newAddr[0] + ' ' +  newAddr[1] + '   </p>';
                setAddr(newAddr[0] + ' ' + newAddr[1])

                setLongitude(mouseEvent.latLng.getLng());
                setLatitude(mouseEvent.latLng.getLat());
                const content = displayAddr;
    
                // ÎßàÏª§Î•º ÌÅ¥Î¶≠Ìïú ÏúÑÏπòÏóê ÌëúÏãúÌï©ÎãàÎã§ 
                marker.setPosition(mouseEvent.latLng);
                marker.setMap(map);
    
                // Ïù∏Ìè¨ÏúàÎèÑÏö∞Ïóê ÌÅ¥Î¶≠Ìïú ÏúÑÏπòÏóê ÎåÄÌïú Î≤ïÏ†ïÎèô ÏÉÅÏÑ∏ Ï£ºÏÜåÏ†ïÎ≥¥Î•º ÌëúÏãúÌï©ÎãàÎã§
                infowindow.setContent(content);
                infowindow.open(map, marker);
            }   
          });
      });
      
      function searchDetailAddrFromCoords(coords, callback) {
          // Ï¢åÌëúÎ°ú Î≤ïÏ†ïÎèô ÏÉÅÏÑ∏ Ï£ºÏÜå Ï†ïÎ≥¥Î•º ÏöîÏ≤≠Ìï©ÎãàÎã§
          if(coords.lng)
            geocoder.coord2Address(coords.lng, coords.lat, callback);
          else
            geocoder.coord2Address(coords.getLng(), coords.getLat(), callback);
      }
  }

  return (
    <div className="flex flex-col items-center">
      {isMapSupported ? (
      <div id="map" style={{ width: '80%', height: 300}}></div>
      ) : null}

      <div style={{marginTop: 20}} className="h-full flex flex-row justify-center items-center pt-5">
        <p style={{marginBottom: 0, fontSize: 13}} className="mr-5">ÎÇòÎäî</p>
        {isMapSupported ? (
          <input 
            type="text"
            placeholder="Í±∞Ï£ºÏßÄ"
            className="bg-gray-100 px-5 py-3"
            value={addr}
            onChange={(e) => locationTextChangeHandler(e)}
            style={{width: 190}}
          />
        ) : (
          <input 
              type="text"
              placeholder="Í±∞Ï£ºÏßÄ"
              className="bg-gray-100 px-5 py-3"
              onChange={(e) => locationTextChangeHandler(e)}
              style={{width: 190}}
          />
        )}
        <p style={{fontSize: 13}} className="text-lg ml-5">Ïóê ÏÇΩÎãàÎã§.</p>
      </div>
      <button onClick={(e) => locationBtnHandler(e)} className="w-full rounded-lg px-5 py-3 my-20 bg-gray-400 text-white focus:outline-none">ÎßûÏäµÎãàÎã§.</button>
    </div>
  )
}
export default KakaoMap